@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.finance-page {
  padding-bottom: 112.37px;

  &__controls {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 12px;

    margin-bottom: 48px;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }
  }

  &__tab {
    padding: 8px 24px !important;

    &:hover {
      color: var.$light;
      background: var.$primary
    }
  }
}

.finance-block {
  margin-bottom: 48px;

  @include mixin.small-tablet {
    margin-bottom: 64px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__subtitle {
    margin-top: 16px;
  }

  &__body {
    margin-top: 28px;

    @include mixin.small-tablet {
      margin-top: 12px;
    }

    & > div {
      margin-top: 20px;
    }
  }
}

.finance-filter {
  color: var.$gray2;

  &__dropdown-btn {
    display: flex;
    align-items: center;
    gap: 4px;
    padding: 8px 16px;
    box-sizing: border-box;

    span {
      color: var.$gray2;
      font-family: var.$font-family;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      letter-spacing: 0.5px;
    }

    svg {
      width: 16px;
      fill: var.$secondary;
    }
  }

  &__list {
    a {
      white-space: nowrap;
    }
  }
}